header{
  position: relative;
  height: 70px;
  width: 100%;
  border-bottom: 1px solid #dce1e4;
}
.logo{
  display: flex;
  align-items: center;
  font-weight: bold;

  top: 30px;
  left: 28px;
  color:#567189;
  position: absolute;
  }
  .logo img{
    border-radius: 50%;
    width: 50px;
    height: 50px;
   padding: 3px;
    object-fit: cover;
  }
.header_main{
  display: flex;
  float: right;
  padding:7px ;
  margin: 5px;
}
.header_main a{
  padding: 10px;
  margin: 5px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
  color:#ff014f;
}
.header_main a:hover{
  color:#ff014f;
}
.sidebar {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 10px;
  right: 0;
  box-shadow: 0 0 2px #0009;
  background-color: white;
  overflow: hidden;
  transition: 0.5s;
  padding-top: 6px;
}
.sidebar.active {
  width: 250px;
  transition: 0.5s;
}
.sidebar a {
  position: relative;
  border-collapse: collapse;

  top:100px;

  text-transform: uppercase;
  padding: 10px 10px 10px 20px;
  text-decoration: none;
  font-size: 14px;
  color: #111;
  display: block;
  transition: 0.3s;
  
 
}

.sidebar a:hover {
  color: rgb(243, 160, 7);
  transition: 0.1s;
  -webkit-transition: 0.1s;
  -moz-transition: 0.1s;
}
.sidebar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}
.small_icon{
  display: none;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
  border-radius: 50%;
 position: relative;
  height: 50px;
  width: 50px;
}
.small_icon i{
  position: absolute;
  color: #ff014f;
  top: 17px;
  left: 16px;
 
  font-size: 20px;
  
}
.main{
  height: auto;
  position: relative;
  bottom: 1vh;
}
.about_contain{
  margin-top: 25px;

  height: auto;
  display: flex;
    
flex-direction: column;
    
    align-items: center;
}
.row{
  position: relative;
  display: flex;
  margin: 20px;
  padding: 5px;
  height: auto;
justify-content:space-evenly;

}
.colm-1{
  display: flex;
  flex-basis: 35%;
  margin: 4px;
  min-height: 350px;
  position: relative;
  min-width: 20vh;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);;
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;

}
.colm-2{
  display: flex;
  flex-basis: 50%;
  margin-left: 20px;
  min-height: 40vh;
  position: relative;
  top:10px;
 color: #ff014f;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
  height: 480px;
  width: 60vh;
  border-radius: 10px;

}


.colm-3,.colm-3-1,.colm-3-2{
  
  height: auto;
  
 
  position: relative;
  
  color: #ff014f;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
justify-content: space-around;
align-items: center;
height: 100px;
bottom: 10px;

  
  width: 90%;

}

.colm-2 img{
  width: 100%;
  height: auto;
  object-fit: cover;
  padding: 15px;
  border-radius: 10px;
  overflow: hidden;
 transition:  0.4s  ease;
  
}

.colm-2 img:hover{
  transform: scale(1.02);
  overflow:  hidden;
}




@media only screen and (max-width:600px){

  .row{
    
    flex-direction: column;
    left: 3px;
    
  }
  .colm-2,.colm-1{
    flex: 1;
    height: fit-content;
    margin-left: 0;
    width: 100%;
    
  }
  
}
@media only screen and (max-width:800px){

  .row{
    
    flex-direction: column;
    left: 3px;
    
  }
  .colm-2,.colm-1{
    flex: 1;
    height: max-content;
    margin-left: 0;
    width: 100%;
    
  }
  
}
@media only screen and (max-width:450px){

  .row{
    
    flex-direction: column;
    left: 3px;
    
  }
  .colm-2,.colm-1{
    flex: 1;
    height:max-content;
    margin-left: 0;
    width: 100%;
    
  }
  
}
@media only screen and (max-width:300px){

  .row{
    
    flex-direction: column;
    left: 3px;
    
  }
  .colm-2,.colm-1{
    flex: 1;
    height: 300px;
    margin-left: 0;
    width: 100%;
    overflow: auto;
    
  }
  
}
.user-info{
  padding: 20px;
  margin-top: 5px;
}
.title div{
  color: #ff014f;
}
.title{
  font-size: 45px;
  font-family: "Poppins"serif;
}
.info{
  padding: 2px;
  margin: 2px;
}
.info span{
  padding: 2px;
  margin: 2px;
  font-size: 20px;
  font-family: "Inter",sans-serif;
  font-weight: 500;

}
.fa-envelope,.fa-location-dot{
  color: #ff014f;
  font-size: 18px;
}
.fa-user{
  color:black;
}
.user::before{
  content: "";
  position: absolute;
  transition:0.4s;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  top: 16px;
  left: 8px;
  background:#ff014f;
  opacity: 0.7;
  z-index: 1;
}
.user::after{
  content: "";
  position: absolute;
  transition: 0.4s;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  top: 36px;
  left: 28px;
  background:#ff014f;
  opacity: 0.2;
  z-index: 1;
  transform: translate(-50%, -50%);
}
.img_container{
  position:relative;
   
   top: 4px;
   border-radius: 8px;
  
}
.img_container img{
  display: block;
  height:190px;
 width:200px;

object-fit: contain;

}
.about_contain{
  border-top: 1px solid #dce1e4;
}
.about_contain h1{
  font-size: 40px;
  padding: 10px;
  text-align: center;
  color:black;
  transition-delay: 3s;
  transition-duration: 3s;

}
.row1{
  
  padding: 10px;
  margin: 20px;
  border-radius: 10px;
  
  width: 70%;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
  
  min-height: 220px;
  transform: 1s ease;
 


}
.ifo{
  display: flex;
  flex-basis: 70%;
  
}
@keyframes amination1 {
  from{
    margin-left: 100px;
    transform: scale(0);

  }
  to{
  margin-left: 10px;
  transform: scale(1);
  }
  
}
@-webkit-keyframes amination1 {
  from{
    margin-left: 100px;
    transform: scale(0);
  }
  to{
    margin-left: 10px;
    transform: scale(1);
  }
  
}
@media only screen and (max-width:450px){

  .row1{
    
    flex-direction: column;
    position: relative;
    min-height: 200px;
    
    width: 80%;
    
  }
  .ifo{
    display: flex;
    flex-direction: column;
    flex-basis: 70%;
    align-items: center;
  }
  .img_container {
  position: absolute;
  top:4rem;

  }
  .info span{
    
    font-size: 12px;
    font-family: "Inter",sans-serif;
   
  }
  .about_contain h1{
    font-size: 10px;
  }
}

@media only screen and (max-width:600px){

  .row1{
    
    flex-direction: column;
    position: relative;
    min-height: 200px;
    
    width: 80%;
    border-radius: 2px;
    
    
  }
  .header_main a{
    display:none;
  
  }
  .small_icon{
    display:block;
  }
  .img_container {
    position: relative;
    top:4rem;
    
    }
    .ifo{
      display: flex;
      flex-direction: column;
      flex-basis: 70%;
      align-items: center;
    }
}
@media only screen and (max-width:780px){

  .row1{
    
    flex-direction: column;
    position: relative;
    min-height: 200px;

    width: 80%;
    border-radius: 2px;
    
  }

  .about_contain h1{
    font-size: 20px;
  }
  .img_container {
    position: relative;
    top:0;
    
    }
    .ifo{
      display: flex;
      flex-direction: column;
      flex-basis: 70%;
      align-items: center;
    }
}
.edu_info {
  color: #ff014f;
  position: relative;
  font-size: 1rem;
  padding: 10px;
  
  margin: 10px;
  -webkit-animation: amination1 0.5s;
  animation: amination1 2s;
}

.year_contian h2{
  background-color: #ff014f;
  border-radius: 10px;
  font-size: 11px;
  color: white;
  width: max-content;
  
  
  padding: 6px;
}
.role{
  color:black;
  font-size: 28px;
  
}
.edu_info span{
  color:#567189;
  font-size: 18px;
  }
.sticky {
  position: fixed;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  top: 0;
   width: 100%;
   transition: 0.5s ease-in;
  border-bottom: 0.5px solid #ccc;
   z-index: 1;
   animation: amination2 1s;
  }
  @keyframes amination2 {
    from{
      margin-top: -40px;
      }
    to{
     margin-top: 0;
    
    }
    
  }
  @-webkit-keyframes amination1 {
    from{
      margin-top: 40px;
    
    }
    to{
     margin-top: 0px;
    }
    
  }
 .contact_cv{
  display: flex;
  height: fit-content;
  margin-top: 15px;
  justify-content: center;
  
 }
.user-info button{
  position: relative;
  top: 30px;

  padding: 5px;
  width: max-content;
  margin: 15px;
  color:#ff014f;
  background: linear-gradient(145deg, #e2e8ec, #ffffff);
  box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
  font-size: 18px;
   padding: 10px 20px;
  border-radius: 2px;
  border: none;
  cursor: pointer;
}  
.user-info button:hover{
  background:linear-gradient(135deg,rgba(6,147,227,1) 0%,rgb(155,81,224) 100%);
  color: white;
  transition: 0.5s ease;
  transform: translateY(-5px);
}
